import { Component, OnInit } from '@angular/core';
import { debug } from 'util';

@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.css']
})
export class BookComponent implements OnInit {
  textvalue='';
  textten='';
  ddlvalue='0';
  colorchecktext=false;
  currentClass={ blackcheck:this.colorchecktext , redcheck:!this.colorchecktext };
  arrTest=[
    { so:1,chu:'a',bool:true},
    { so:2,chu:'b',bool:false}
  ];
  constructor() { }
  
    ngOnInit() {
    }
  checkText(txtname){
    if(txtname=='')
    {
      this.colorchecktext=false;
    }
    else
    {
      this.colorchecktext=true;
    }
    
  }
  addText()
  {
    this.arrTest.unshift({
      so: this.textvalue.length,
      chu: this.textten,
      bool: true
    });
    this.textvalue='';
    this.textten='';
  }
  removeText(so)
  {
    const index=this.arrTest.findIndex(x=>x.so===so);
    this.arrTest.splice(index,1);
  }
  chooseddl(itembool)
  {
     const chooseall=this.ddlvalue==='0';
     const choosetrue=this.ddlvalue==='1' && itembool;
     const choosefalse=this.ddlvalue==='2' && !itembool;
     return chooseall||choosetrue||choosefalse;
  }
}



