import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-listperson',
  templateUrl: './listperson.component.html',
  styleUrls: ['./listperson.component.css']
})
export class ListpersonComponent implements OnInit {
  arraylistperson=[
    {
      so:1,chu:'mot',bool:true
    },
    {
      so:2,chu:'hai',bool:false
    }
  ];
  constructor() { }

  ngOnInit() {
  }
  removePersonClick(so:Number){
    const removeNumber=this.arraylistperson.findIndex(x=>x.so===so);
    this.arraylistperson.splice(removeNumber,1);
  }
}



