import { Component,OnInit } from '@angular/core';
import { IpService } from './ip.service';
import { debug } from 'util';
@Component({
  selector: 'app-ip',
  template:`{{messageWeather()}}
  <input type="text" [(ngModel)]="txtweather" />
  <p><button (click)="clickweather()">Click chơi</button></p>
  `,
  providers:[IpService]
})
export class IpComponent implements OnInit {
  ip:String;  
  txtweather='';
  city='';
  temp='';
  isLoading=false;
  constructor(private ipService:IpService){    
   }
  ngOnInit() {
    
  }
  messageWeather(){
    if(!this.isLoading)
    {
      if(this.city !='' && this.ip!='')
      {
        return this.city + ' ' +this.ip;
      }
      else
      {
        return '';
      }
    }
    else
    {
      return 'Loading...';
    }
  }
  clickweather()
  {
    this.isLoading=true;
    this.ipService.getIp(this.txtweather)
    .then(ip=>{
      this.ip=ip,
      this.city=this.txtweather,
      this.isLoading=false;
    })
    .catch(ip=>{
      this.ip='ko co',
      this.city=this.txtweather,
      this.isLoading=false;
    });
  }

  
}



